import React from 'react'
import {Card, Col, Row, Spin, Icon, Alert, Switch, Button} from 'antd'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import CustomBreadcrumb from '../../../components/CustomBreadcrumb/index'
import TypingCard from '../../../components/TypingCard'

class SpinDemo extends React.Component {
  state = {
    loading: false,
    loading2: false
  }
  componentWillMount(){
    //React
    //vuehttps://segmentfault.com/q/1010000006653683/a-1020000007724198
    NProgress.start()
  }
  componentDidMount(){
    NProgress.done()
  }
  componentWillUnmount(){
    //NProgress.start()
    NProgress.done()
  }
  NProgressStart = () => {
    NProgress.start()
    this.setState({
      loading2: true
    })
  }
  NProgressDone = () => {
    NProgress.done()
    this.setState({
      loading2: false
    })
  }

  render() {
    const loading2 = this.state.loading2
    return (
      <div>
        <CustomBreadcrumb arr={['','']}/>
        <TypingCard source=''/>
        <Row gutter={16}>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Spin/>&emsp;
              <Spin indicator={<Icon type='loading'/>}/>
            </Card>
            <Card bordered={false} className='card-item'>
              <Spin tip="Loading...">
                <Alert
                  message="Alert message title"
                  description="Further details about the context of this alert."
                  type="info"
                />
              </Spin>
            </Card>
            <Card bordered={false} className='card-item'>
              <Button onClick={this.NProgressStart} loading={loading2}></Button>&emsp;
              <Button onClick={this.NProgressDone}></Button>
            </Card>
          </Col>
          <Col span={12}>
            <Card bordered={false} className='card-item'>
              <Spin size='small'/>&emsp;
              <Spin/>&emsp;
              <Spin size='large'/>
            </Card>
            <Card bordered={false} className='card-item'>
              <div style={{marginBottom: '1em'}}>
                <Spin tip="Loading..." spinning={this.state.loading}>
                  <Alert
                    message="Alert message title"
                    description="Further details about the context of this alert."
                    type="info"
                  />
                </Spin>
              </div>
              Loading state<Switch onChange={(checked) => this.setState({loading: checked})}/>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}

export default SpinDemo


